* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  font-size: clamp(.7rem,-.875rem + 6.333333vw,1.3rem);
  height: 100vh;
  overflow-x: auto;
  width: 100%;
  background: #0c0c0c;
  color: white;
}

h1 {
  margin-top: 1vh;
  text-align: center;
  text-decoration: underline;
}

.projectsHolder {
  display: flex;
  height: fit-content;
  padding: 1.5vh 0 3.5vh 0;
  align-items: flex-start;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 3vh 3vw;
}

main {
  text-align: center;
  vertical-align: center;
  line-height: 100vh
}


.project {
  color: white;
  border: 1px solid #353535;
  border-radius: 2px;
  box-shadow: 0 3px 6px #00000029,0 3px 6px #0000003b;
  height: 40vh;
  max-width: 250px;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  padding: 1vh;
  transition: transform .2s;
  width: 42vw;
  background: #2b2b2b;
}

.project:hover, 
.project:active {
  transform: scale(0.98);
  box-shadow: unset;
}

.projectImage {
  height: 80%;
  width: 100%;
}

a {
  text-decoration: unset;
  color: black;
}

img {
  height: 100%;
  width: 100%;
}

.projectName {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20%;
}